<template>
  <div style=" width:100%; height:1000px;" class="bg-img">
    <div>
      <el-row>
        <div class="login-card">
          <el-col :span="6" :offset="7">
            <div>
              <img src="../../assets/img/login/loginS.png" class="small-img">
            </div>
          </el-col>
          <el-col :span="4">
            <el-card>
              <div slot="header">
                <span class="card-title" style="font-family: 'Microsoft YaHei'">天津临港经济区建设服务中心建筑工程管理系统</span>
              </div>
              <div>
                <div><img src="https://chengfy.com/_uploads/files/staneyffer.jpg" class="user-img"></div>
                <div>
                  <span class="user-name">张小明</span>
                </div>
              </div>
              <el-form ref="form" :label-position="labelPosition" :model="form" label-width="80px">
                <el-form-item>
                  <el-input v-model="form.username" placeholder="用户名"></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model="form.password" type="password" placeholder="密码"></el-input>
                </el-form-item>

                <el-form-item>
                  <el-button type="" @click="userLogin" style="width: 280px; background-color: #345DBA">
                    <span style="color: white">登录</span>
                  </el-button>
                </el-form-item>
                <div>
                  <!--<span class="card-down-text">后台运维</span>-->
                  <img src="../../assets/img/temp/login/loginCard.png" style="float: right">
                </div>
              </el-form>

            </el-card>
          </el-col>
        </div>
      </el-row>
    </div>
  </div>
</template>

<script>
  import authApi from '../../api/auth/user'
  import router from '../../router'
  import {mapActions} from 'vuex'
  export default {
    name: 'login',
    data () {
      return {
        labelPosition: 'top',
        form: {
          account: 'admin',
          password: '1'
        },
        userData: {
          token: null,
          user: null
        }
      }
    },
    methods: {
      ...mapActions([
        'login'
      ]),
      userLogin () {
        // 'sws001'
        // console.log(this.form)
        authApi.userLogin(this.form).then(res => {
          // console.log(res.status)
          let resData = res.data
          this.login(resData)
          // alert(resData.sysUser)
          router.push({
            path: '/index'})
        }).catch(res => {
          console.log(res)
        })
      }
    }
  }
</script>

<style scoped>
  .bg-img {
    background-image: url('../../assets/img/login/loginBg.png');
  }

  .small-img {
    height: 482px;
    width: 450px;
    /*position:absolute;*/
    /*border-right: 0;*/
    float: right;
  }

  .login-card {
    margin-top: 200px;
  }

  .card-title {
    font-size: 12px;
    margin-left: 25px;
    /*position  : absolute;*/
    /*top       : 50%;*/
    /*left      : 50%;*/
    /*transform : translate(-50%,-50%);*/
  }

  .user-img {
    border-radius: 50%;
    margin-top: 8px;
    margin-left: 35%;
    height: 75px;
    width: 75px;
  }

  .user-name {
    font-size: 18px;
    margin-left:  35%;
  }

  .card-down-text {
    font-size: 15px;
    float: right;
  }
</style>



